@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin xl_desktop {
    @media (min-width: v.$xl_desktop) {
        @content;
    }
}

@mixin button($bgColor, $textColor) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $textColor;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken($bgColor, 10%);
        cursor: pointer;
    }
}

@mixin grid($columns, $gap) {
    display: grid;
    gap: $gap;
    grid-template-columns: repeat($columns, 1fr);
}

@mixin container {
    width: min(95%, 140rem);
    margin: 0 auto;
}

@mixin sm-container {
    width: min(95%, 60rem);
    margin: 0 auto;
}
@mixin headings{
    font-family: v.$main-font;
    font-weight: v.$black-font;
    margin: 2rem 0;
}

@mixin page-name{
    @include headings;
    margin: 6rem 0 3rem 0;
    text-align: center;
}

@mixin fluid-text ($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw ,$max);
}

@mixin gradient-text($start, $end){
    background: linear-gradient(to right, $start, $end);
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

@mixin logo(){
    color: v.$white;
    text-align: center;
    margin-bottom: 2rem;
    @include desktop {
        text-align: left;
        margin: 0;
    }
}

@mixin social-menu($color, $size){
    &__link{
        &::before{
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            font-family: "Font Awesome 6 Brands";
        }

        &[href*="https://facebook.com"]::before {
            content: "\f39e";
        }
        &[href*="https://github.com"]::before {
            content: "\f09b";
        }
        
        &[href*="https://tiktok.com"]::before {
            content: "\e07b";
        }
        &[href*="https://instagram.com"]::before {
            content: "\f16d";
        }
        &[href*="https://youtube.com"]::before {
            content: "\f167";
        }
        &[href*="https://twitter.com"]::before {
            content: "\f099";
        }
    }

    &__hide{
        display: none;
    }
}